.footer{
    width: 100%;
    max-width: 1024px;
    font-size: 12px;    
}
.fLists{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}
.fList{
    list-style: none;
    padding: 0;

}
.fListItem{
    margin-bottom: 10px;
    color: #003580;
    cursor: pointer;
}
.fText{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 1rem;
    margin-bottom: 10px;
}